.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.newItem {
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 10px;
  width: 500px;
  line-height: 30px;
  margin-bottom: 30px;

  height: 30px;

  font-family: 'Bebas Neue',-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;

    font-size: 20px;
    /* border: 1px solid grey; */
  
}

.submit{
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 10px;
  width: 100px;
  line-height: 30px;
  margin-bottom: 30px;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  
}

.submit:hover{
  background-color: #c8c7ca;
}

#app-name{
  font-size: 100px;
}


